import React from 'react'
import styles from './Movie.module.css'

export default function Movie(props) {
  return (
    <div className={styles.root}>
      <div className={styles.videoWrapper}>
        {
          props.showMovie && <video
            className={styles.video}
            src={props.src}
            controls={'controls'}
          />
        }
      </div>
    </div>
  )
}