import React, {useState} from 'react'
import styles from './Share.module.css'

import { ReactComponent as ShareIcon } from '../../assets/svg/share.svg'
import { ReactComponent as Fb } from '../../assets/svg/facebook.svg'
import { ReactComponent as Tw } from '../../assets/svg/tw.svg'
import { ReactComponent as Vk } from '../../assets/svg/vk.svg'
import { ReactComponent as Ok } from '../../assets/svg/odn.svg'
import { ReactComponent as Tel } from '../../assets/svg/tel.svg'

const shareData = {
  title:  document.querySelectorAll('meta[property="og:title"]')[0].content || '',
  url: process.env.NODE_ENV === 'development' ? window.location.href : 'https://greensleeves.github.io/',
  image: document.querySelectorAll('meta[property="og:image"]')[0].content || '',
  description: document.querySelectorAll('meta[property="og:description"]')[0].content || ''
};

const socials = [
  {
    name: 'fb',
    icon: <Fb/>,
    hash: `https://www.facebook.com/dialog/feed?app_id=1567926033533341&display=popup&link=${encodeURI(shareData.url)}&name=${encodeURI(shareData.title)}&description=${encodeURI(shareData.description)}&picture=${encodeURI(shareData.url).replace(/\/$/, '')}${shareData.image}`
  },
  {
    name: 'tw',
    icon: <Tw/>,
    hash: `https://twitter.com/intent/tweet?text=${encodeURI(shareData.title)}&url=${encodeURI(shareData.url)}&hashtags=&utm_source=share2`
  },
  {
    name: 'vk',
    icon: <Vk/>,
    hash: `https://vk.com/share.php?url=${encodeURI(shareData.url)}&title=${encodeURI(shareData.title)}&description=${encodeURI(shareData.description)}&image=${shareData.image}`
  },
  {
    name: 'ok',
    icon: <Ok/>,
    hash: `https://connect.ok.ru/dk?st.cmd=WidgetSharePreview&st.shareUrl=${encodeURI(shareData.url)}&st.title=${encodeURI(shareData.title)}&st.description=${encodeURI(shareData.description)}&utm_source=share2`
  },
  {
    name: 'tel',
    icon: <Tel/>,
    hash: `https://tttttt.me/share/url?url=${encodeURI(shareData.url)}&text=${encodeURI(shareData.title)}`
  }
]

export default function Share (props) {

  const [showSocials, setShowSocials] = useState(false);

  const onClickSocialHandler = (social, e) => {
    e.stopPropagation();

    window.open(
      social.hash,
      null,
      'height=400,width=600,status=yes,toolbar=no,menubar=no,location=no'
    );
  }

  const toggleShareHandler = () => {
    setShowSocials(!showSocials)
  }

  return (
    <div className={styles.root}>
      <div className={styles.share} onClick={toggleShareHandler}>
        <ShareIcon/>
      </div>
      {
        showSocials && <div className={styles.socials}>
          {
            socials.map(social => {
              return <button className={styles.shareItem} key={social.name} onClick={onClickSocialHandler.bind(this, social)}>
                <div className={styles.shareItemIcon}>
                  {social.icon}
                </div>
              </button>
            })
          }
        </div>
      }
    </div>
  )
}
